	module.exports = {"data":{"post":{"html":"<p>Wonderful weather today. Sunny with light clouds, slight breeze, 28&deg;C (83&deg;F). But most folks around here are complaining that it's too hot. One could think that Germans just always like to complain about the weather. I'm more polite and say that they have a narrow comfort zone. (I will admit, though, that most buildings here are not designed for maximum comfort in warm weather.)</p>\n<p>Hot, by the way, begins at 40&deg;C.</p>","fields":{"title":"Comfort Zone","date":"02 Jun 2003","url":"https://papascott.de/2003-06-02-comfort-zone/"}}},"pathContext":{"slug":"/2003-06-02-comfort-zone/","prev":{"fields":{"title":"Blog! Huh! What Is It Good For?","date":"02 Jun 2003","slug":"/2003-06-02-blog-huh-what-is-it-good-for/"}},"next":{"fields":{"title":"feed on feeds","date":"02 Jun 2003","slug":"/2003-06-02-feed-on-feeds/"}}}}

